*{
    margin: 0;
    padding:0
}

.adminPageHeader{
    width: 100%;
    text-align: center;
    padding: 10px;
    font-size: 18px;
    background-color: #424F63;
    color:#d6f3e9;
}

.adminPagePanel{
    background-color: #7d8ca3;
    height:100vh;
    overflow-y: scroll
}

.adminPageMain{
    height:100%;
    padding:20px
}

.adminSubHeader{
    width:100%;
    font-size:18px;
    font-weight: 600;
    color: teal;
    margin-left:5px
}

.adminHr{
    margin:5px
}

.adminSubIcon{
    margin-right:10px;
}

.adminTableHeader{
    background-color: #424F63
}

.adminButton{
    font-size: 14px
}

.adminFormik{
   height: 88%;
   overflow-y: scroll;
}

.adminFormMain{
    height: 85vh;
    overflow: hidden;
    position: relative;
}

.adminFormFooter{
    position: absolute;
    width: 100%;
    bottom: 0;
    padding:16px;
}

.adminFormLoader{
    position:absolute;
    height:100%;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    touch-action: none;
    z-index: 1;
    background-color: rgb(99, 115, 121);
    opacity:.6;
}

.notification{
    display: flex;
    padding:5px 10px;
    width:100%
}

.notification-icon{
    flex:1 1;
    justify-content: flex-start;
    align-items: center;
}

.notification-message{
    flex:5 1;
    min-height: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
}

.notification-remove{
    flex:1 1;
    display:flex;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
}

.admin{
    position: relative;
    overflow: hidden;
}
.notification-panel{
    position:absolute;
    right:50px;
    bottom:30px;
    display:flex;
    flex-direction:column;
    z-index: 10000;
}

.noitifcation-panel-bottom{
    display: flex;
    align-items: center;
    justify-content: center;
}

.notification-buttons {
    display: inline-block;
    border-radius: 60px;
    font-size: 18px;
    box-shadow: 0px 0px 8px #888;
    padding: 0.5em 0.6em;
    background-color: rgb(232, 232, 233);
    cursor: pointer;
  }

.login-main{
    height: 100vh;
    background-color: #7d8ca3;
}

.login-portal{
    padding: 0;
    overflow: hidden;
}

.login-header{
    background-color: #424F63;
    color:white;
    text-align: center;
    font-size: 18px;
}
